<template>
    <div>
        <div style="width:100%; text-align: center;">
            <img class="qrImg" src="../assets/images/二维码.png"/>
        </div>
        <div style="padding-bottom: 60px;">
            <ul class="applyTip">
                <li class="tipText">长按图片识别二维码</li>
                <li class="tipText">下载APP即可立即申请贷款</li>
                <li class="share-text">
                    <div style="width:100px">申贷准备证件：</div>
                    <div style="width:190px">本人身份证原件、经营企业营业执照原件、按揭贷款对应房产证（不动产权证书）原件或购房合同原件，如已婚请提供配偶身份证及结婚证原件</div> 
                </li>
                <li class="tipText" style="display:block;text-align:center;margin-top:20px;">如有疑问，可咨询浙商银行服务热线95527</li>
            </ul>
        </div>
        <div class="share-pop" v-if="shareShow" @click="close">
            <img src="../assets/images/share.png" alt="">
        </div>
        <div class="next-box">
            <div class="next" @click="share">分享</div>
        </div> 
    </div>
</template>

<script>
export default {
    name: 'share',
    data() {
        return{
            shareShow: false
        }
    },
    methods: {
        share() {
            this.shareShow = true;
        },
        close() {
            this.shareShow = false;
        }
    }
}
</script>
<style lang="scss">
@import '../assets/css/share.css';
.share-pop{
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    top: 0;
    left: 0;
    z-index: 99;
    img{
        position: absolute;
        top: 20px;
        right: 10px;
        width: 150px;
    }
}
.share-text{
    display: flex;
}
</style>



