<template>
    <div>
        <p class="prompt-text">请尽量填写真实信息，信息越真实，获得的额度越高！</p>
        <div class="ques-content margin10">
            <div class="ques-item padding4">
                <input type="text" class="ques-input" value="您在银行及小贷公司是否有未结清贷款" readonly>
            </div>
            <div class="radio-item">
                <label class="radio-box" for="bankTrue">
                    <img src="../assets/images/true.png" alt="" class="radio-img" v-if="bank == 0"> 
                    <img src="../assets/images/false.png" alt="" class="radio-img" v-else>
                    <input type='radio' id="bankTrue" v-model="bank" value="0" hidden>
                    <span class="radio-text">是</span>
                </label>
                <label class="radio-box" for="bankFalse">
                    <img src="../assets/images/true.png" alt="" class="radio-img" v-if="bank == 1"> 
                    <img src="../assets/images/false.png" alt="" class="radio-img" v-else>
                    <input type='radio' id="bankFalse" v-model="bank" value="1" hidden>
                    <span class="radio-text">否</span>
                </label>
            </div>
            <div class="ques-item border padding10">
                <div class="ques-text">未还清贷款银行或小贷机构数量</div>
                <input type="number" class="input-samll" v-model="loannum" :disabled="isBankReadOnly" placeholder="若无请忽略">
                <div class="ques-unit">个</div>
            </div>
            <div class="ques-item padding10">
                <div class="ques-text">未还清贷款余额</div>
                <input type="number" class="input-samll" v-model="loanbalance" :disabled="isBankReadOnly" placeholder="若无请忽略">
                <div class="ques-unit">元</div>
            </div>
        </div>
        <div class="ques-content margin10">
            <div class="ques-item padding4">
                <input type="text" class="ques-input" value="您在P2P及网贷平台是否有未结清贷款" readonly>
            </div>
            <div class="radio-item">
                <label class="radio-box" for="p2pTrue">
                    <img src="../assets/images/true.png" alt="" class="radio-img" v-if="p2p == 0"> 
                    <img src="../assets/images/false.png" alt="" class="radio-img" v-else>
                    <input type='radio' id="p2pTrue" v-model="p2p" value="0" hidden>
                    <span class="radio-text">是</span>
                </label>
                <label class="radio-box" for="p2pFalse">
                    <img src="../assets/images/true.png" alt="" class="radio-img" v-if="p2p == 1"> 
                    <img src="../assets/images/false.png" alt="" class="radio-img" v-else>
                    <input type='radio' id="p2pFalse" v-model="p2p" value="1" hidden>
                    <span class="radio-text">否</span>
                </label>
            </div>
            <div class="ques-item border padding10">
                <div class="ques-text">未还清贷款平台数量</div>
                <input type="number" class="input-samll" v-model="p2pnum" :disabled="isP2pReadOnly" placeholder="若无请忽略">
                <div class="ques-unit">个</div>
            </div>
            <div class="ques-item padding10">
                <div class="ques-text">未还清贷款余额</div>
                <input type="number" class="input-samll" v-model="p2pbalance" :disabled="isP2pReadOnly" placeholder="若无请忽略">
                <div class="ques-unit">元</div>
            </div>
        </div>
        <div class="next-box">
            <div class="next" @click="next">提交</div>
        </div> 
        <mt-popup v-model="error" class="mint-popup-2" position="top" :modal="false">
            {{errText}}
        </mt-popup>  
    </div>
</template>

<script>
export default {
    name: 'Ques4',
    data(){
        return{
            bank: this.$store.state.bank,
            p2p: this.$store.state.p2p,
            loannum: this.$store.state.data.loannum,
            loanbalance: this.$store.state.data.loanbalance,
            p2pnum: this.$store.state.p2pnum,
            p2pbalance: this.$store.state.p2pbalance,
            error: false,
            errText: '',
            isBankReadOnly: false,
            isP2pReadOnly: false
        }
    },
    watch:{
        bank: function(){
            if(this.bank == 1){
                this.isBankReadOnly = true;
                this.loannum = 0;
                this.loanbalance = 0;
                this.$store.commit('LOANNUM',this.loannum)
                this.$store.commit('LOANBALANCE',this.loanbalance)
            }else if(this.bank == 0){
                this.isBankReadOnly = false;
                this.loannum = '';
                this.loanbalance = '';
                this.$store.commit('LOANNUM',this.loannum)
                this.$store.commit('LOANBALANCE',this.loanbalance)
            }
            this.$store.commit('BANK',this.bank)
        },
        p2p: function(){
            if(this.p2p == 1){
                this.isP2pReadOnly = true;
                this.p2pnum = 0;
                this.p2pbalance = 0;
                this.$store.commit('P2PNUM',this.p2pnum)
                this.$store.commit('P2PBALANCE',this.p2pbalance)
            }else if(this.p2p == 0){
                this.isP2pReadOnly = false;
                this.p2pnum = '';
                this.p2pbalance = '';
                this.$store.commit('P2PNUM',this.p2pnum)
                this.$store.commit('P2PBALANCE',this.p2pbalance)
            }
            this.$store.commit('P2P',this.p2p)
        },
        loannum: function(){
            this.$store.commit('LOANNUM',this.loannum)
        },
        loanbalance: function(){
            this.$store.commit('LOANBALANCE',this.loanbalance)
        },
        p2pnum: function(){
            this.$store.commit('P2PNUM',this.p2pnum)
        },
        p2pbalance: function(){
            this.$store.commit('P2PBALANCE',this.p2pbalance)
        },
        error: function(val) {
            if (val) {
                setTimeout(() => {
                    this.error = false;
                }, 2000);
            }
        }
    },
    methods: {
        next() {
            if(this.bank == '-1' || this.p2p == '-1'){
                this.errText = '请将信息补充完整';
                this.error = true;
                return false;
            }
            if(this.bank == 0){
                if(!this.loannum){
                    this.errText = '请填写未还清贷款银行或小贷机构数量';
                    this.error = true;
                    return false;
                }
                if(!this.loanbalance){
                    this.errText = '请填写未还清贷款余额';
                    this.error = true;
                    return false;
                }
            }
            if(this.p2p == 0){
                if(!this.p2pnum){
                    this.errText = '请填写未还清贷款平台数量';
                    this.error = true;
                    return false;
                }
                if(!this.p2pbalance){
                    this.errText = '请填写未还清贷款余额';
                    this.error = true;
                    return false;
                }
            }
            if(this.p2p == 1 && this.loannum < 5 && this.loanbalance <= 5000000){
                this.$http.post('https://access.yuanbaopu.com/zsdatasubmit', 
                    this.$qs.stringify(this.$store.state)
                ).then(result => {
                    if(result.data.errmsg != ""){
                        this.$router.push('fail');
                    }else{
                        this.$store.commit('AMOUNT',result.data.amount);
                        this.$router.push('success');
                    }
                })
            }else{
                this.submit();
                this.$router.push('fail');
            }
        }
    }
}
</script>
<style lang="scss" scoped>
    
</style>






