<template>
    <div>
        <p class="prompt-text">请尽量填写真实信息，信息越真实，获得的额度越高！</p>
        <div class="ques-content margin10">
            <p class="ques2-title">您是否在以下银行中有还款中的按揭房贷？</p>
            <img src="../assets/images/bank-list.png" alt="" class="bank-img">
            <div class="padding4">
                <label class="radio-box" for="loanTrue">
                    <img src="../assets/images/true.png" alt="" class="radio-img" v-if="loan == 0"> 
                    <img src="../assets/images/false.png" alt="" class="radio-img" v-else>
                    <input type='radio' id="loanTrue" v-model="loan" value="0" hidden>
                    <span class="radio-text">是</span>
                </label>
                <label class="radio-box" for="loanFalse">
                    <img src="../assets/images/true.png" alt="" class="radio-img" v-if="loan == 1"> 
                    <img src="../assets/images/false.png" alt="" class="radio-img" v-else>
                    <input type='radio' id="loanFalse" v-model="loan" value="1" hidden>
                    <span class="radio-text">否</span>
                </label>
            </div>
        </div>
        <mt-popup v-model="error" class="mint-popup-2" position="top" :modal="false">
            {{errText}}
        </mt-popup>
        <div class="next-box">
            <div class="next" @click="next">下一步</div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Ques2',
    data(){
        return{
            loan: this.$store.state.loan,
            error: false,
            errText: ''
        }
    },
    watch:{
        loan: function(){
            this.$store.commit('LOAN',this.loan);
        },
        error: function(val) {
            if (val) {
                setTimeout(() => {
                    this.error = false;
                }, 2000);
            }
        }
    },
    methods:{
        next(){
            if(this.loan == '-1'){
                this.errText = '请将信息补充完整';
                this.error = true;
                return false;
            }
            if(this.loan == '0'){
                this.$router.push('ques3');
            }else{
                this.submit();
                this.$router.push('fail');
            }
        }
    }
}
</script>
<style lang="scss" scoped>
    .ques2-title{
        padding: 0.4rem 0;
        padding-left: 4%;
        margin: 0;
        font-size: 1rem;
        text-align: left;
    }
    .bank-img{
        display: block;
        width: 92%;
        margin: 0 auto 10px;
    }
    .radio-box{
        position: relative;
        display: block;
        height: 40px;
        line-height: 40px;
        font-size: 1rem;
        .radio-img{
            position: absolute;
            width: 15px;
            top: 12px;
            left: 0;
        }
        .radio-text{
            padding-left: 25px;
        }
    }
</style>






