import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({
    state: {
        data: {
            name: "",
            idcard: "",
            phone: "",
            marry: "",
            licence: "",
            city: "",
            addr: "",
            loanamount: "",
            monthpayment: "",
            periods: "",
            loannum: "",
            loanbalance: ""
        },
        loan: "-1",
        addressText: '请选择按揭房产住址',
        overdue: "-1",
        bank: "-1",
        p2p: "-1",
        p2pnum: "",
        p2pbalance: "",
        amount: 0
    },
    actions: {
    },
    mutations: {
        NAME (state, data) {
            state.data.name = data
        },
        IDCARD (state, data) {
            state.data.idcard = data
        },
        PHONE (state, data) {
            state.data.phone = data
        },
        MARRY (state, data) {
            state.data.marry = data
        },
        LICENCE (state, data) {
            state.data.licence = data
        },
        LOAN (state, data) {
            state.loan = data
        },
        ADDRESSTEXT (state, data) {
            state.addressText = data
        },
        CITY (state, data) {
            state.data.city = data
        },
        ADDR (state, data) {
            state.data.addr = data
        },
        LOANAMOUNT (state, data) {
            state.data.loanamount = data
        },
        MONTHPAYMENT (state, data) {
            state.data.monthpayment = data
        },
        PERIODS (state, data) {
            state.data.periods = data
        },
        OVERDUE (state, data) {
            state.overdue = data
        },
        BANK (state, data) {
            state.bank = data
        },
        P2P (state, data) {
            state.p2p = data
        },
        LOANNUM (state, data) {
            state.data.loannum = data
        },
        LOANBALANCE (state, data) {
            state.data.loanbalance = data
        },
        P2PNUM (state, data) {
            state.p2pnum = data
        },
        P2PBALANCE (state, data) {
            state.p2pbalance = data
        },
        AMOUNT (state, data) {
            state.amount = data
        }
    },
    getters: {
    },  
    modules: {
        
    }
})
export default store


