<template>
    <div>
        <p class="prompt-text">请尽量填写真实信息，信息越真实，获得的额度越高！</p>
        <div class="ques-content margin10">
            <div class="ques-item border right">
                <div class="ques-icon">
                    <img src="../assets/images/home.png" alt="" class="ques-img">
                </div>
                <input type="text" class="ques-input" :value="addressText" readonly @click="address">
            </div>
            <div class="ques-item border">
                <div class="ques-icon">
                    <img src="../assets/images/local.png" alt="" class="ques-img">
                </div>
                <input type="text" class="ques-input" v-model="addr" placeholder="请填写详细地址">
            </div>
            <div class="ques-item border">
                <div class="ques-icon">
                    <img src="../assets/images/bank.png" alt="" class="ques-img">
                </div>
                <input type="number" class="ques-input" v-model="loanamount" placeholder="请填写房贷金额">
                <div class="ques-unit">元</div>
            </div>
            <div class="ques-item border">
                <div class="ques-icon">
                    <img src="../assets/images/bill.png" alt="" class="ques-img">
                </div>
                <input type="number" class="ques-input" v-model="monthpayment" placeholder="请填写每月还款金额">
                <div class="ques-unit">元</div>
            </div>
            <div class="ques-item border">
                <div class="ques-icon">
                    <img src="../assets/images/bill.png" alt="" class="ques-img">
                </div>
                <input type="number" class="ques-input" v-model="periods" placeholder="请填写已还款期数">
                <div class="ques-unit">期</div>
            </div>
            <div class="ques-item">
                <div class="ques-icon">
                    <img src="../assets/images/time.png" alt="" class="ques-img">
                </div>
                <input type="text" class="ques-input" value="您在还款过程中是否逾期" readonly>
            </div>
            <div class="radio-item">
                <label class="radio-box" for="overdueTrue">
                    <img src="../assets/images/true.png" alt="" class="radio-img" v-if="overdue == 0"> 
                    <img src="../assets/images/false.png" alt="" class="radio-img" v-else>
                    <input type='radio' id="overdueTrue" v-model="overdue" value="0" hidden>
                    <span class="radio-text">是</span>
                </label>
                <label class="radio-box" for="overdueFalse">
                    <img src="../assets/images/true.png" alt="" class="radio-img"  v-if="overdue == 1"> 
                    <img src="../assets/images/false.png" alt="" class="radio-img" v-else>
                    <input type='radio' id="overdueFalse" v-model="overdue" value="1" hidden>
                    <span class="radio-text">否</span>
                </label>
            </div>
        </div>
        <div class="next-box">
            <div class="next" @click="next">下一步</div>
        </div> 
        <mt-popup v-model="popupVisible" position="bottom" class="mint-popup-4">  
            <div class="picker-toolbar">  
                <span class="mint-datetime-action mint-datetime-cancel" @click="cancleAddress">取消</span>  
                <span class="mint-datetime-action mint-datetime-confirm" @click="selectAddress">确定</span>  
            </div>  
            <mt-picker :slots="addressSlots" @change="onAddressChange" :visible-item-count="3" :valueKey="'text'"></mt-picker>  
        </mt-popup>
        <mt-popup v-model="error" class="mint-popup-2" position="top" :modal="false">
            {{errText}}
        </mt-popup>  
    </div>
</template>

<script>
    const address = [
        {
            "value":"330000",
            "text":"浙江省",
            "children":[
                {
                    "value":"330100",
                    "text":"杭州市"
                },
                {
                    "value":"330200",
                    "text":"宁波市"
                },
                {
                    "value":"330300",
                    "text":"温州市"
                },
                {
                    "value":"330400",
                    "text":"嘉兴市"
                },
                {
                    "value":"330500",
                    "text":"湖州市"
                },
                {
                    "value":"330600",
                    "text":"绍兴市"
                },
                {
                    "value":"330700",
                    "text":"金华市"
                },
                {
                    "value":"330800",
                    "text":"衢州市"
                },
                {
                    "value":"331000",
                    "text":"台州市"
                },
                {
                    "value":"331100",
                    "text":"丽水市"
                }
            ]
        },
        {
            "value":"320000",
            "text":"江苏省",
            "children":[
                {
                    "value":"320100",
                    "text":"南京市"
                }
            ]
        }
    ];
    export default {
        name: 'Ques3',
        data() {
            return {
                popupVisible: false,  
                addressSlots: [
                    {
                        flex: 1,
                        values: address,
                        className: 'slot1',
                        textAlign: 'center'
                    }, {
                        divider: true,
                        content: '-',
                        className: 'slot2'
                    }, {
                        flex: 1,
                        values: ['','南京市'],
                        className: 'slot3',
                        textAlign: 'center'
                    }
                ],
                addressProvince: '江苏省',
                addressCity: '南京市',
                addr: this.$store.state.data.addr,
                loanamount: this.$store.state.data.loanamount,
                monthpayment: this.$store.state.data.monthpayment,
                periods: this.$store.state.data.periods,
                overdue: this.$store.state.overdue,
                error: false,
                errText: ''
            };
        },
        computed: {
            addressText: function() {
                return this.$store.state.addressText;
            },
            cityCode: function() {
                return this.$store.state.data.city;   
            }
        },
        methods: {
            onAddressChange(picker, values) {
                if(!!values[0]){
                    picker.setSlotValues(1, values[0]['children']);
                    this.addressProvince = values[0];
                    this.addressCity = values[1];
                }
            },
            address() {
                this.popupVisible = true;
            },
            cancleAddress() {
                this.popupVisible = false;
            },
            selectAddress() {
                if(!!this.addressCity.value){
                    let cityCode = this.addressCity.value
                    let addressText = this.addressProvince.text+' '+this.addressCity.text;
                    this.$store.commit('ADDRESSTEXT',addressText);
                    this.$store.commit('CITY',cityCode);
                }else{
                    let addressText = '江苏省 南京市';
                    this.$store.commit('ADDRESSTEXT',addressText);
                    this.$store.commit('CITY','320100');
                }
                this.popupVisible = false;
            },
            next() {
                if(this.overdue == '-1'){
                    this.errText = '请将信息补充完整';
                    this.error = true;
                    return false;
                }
                if(!this.cityCode || !this.addr || !this.loanamount || !this.monthpayment || !this.periods){
                    this.errText = '请将信息补充完整';
                    this.error = true;
                }else{
                    if(this.overdue == '1' && this.loanamount >= 100000 && this.monthpayment >= 800 && this.periods >= 12){
                        this.$router.push('ques4');
                    }else{
                        this.submit();
                        this.$router.push('fail');
                    }
                }
                
            }
        },
        watch: {
            addr: function(){
                this.$store.commit('ADDR',this.addr);
            },
            loanamount: function(){
                this.$store.commit('LOANAMOUNT',this.loanamount);
            },
            monthpayment: function(){
                this.$store.commit('MONTHPAYMENT',this.monthpayment);
            },
            periods: function(){
                this.$store.commit('PERIODS',this.periods);
            },
            overdue: function(){
                this.$store.commit('OVERDUE',this.overdue);
            },
            error: function(val) {
                if (val) {
                    setTimeout(() => {
                        this.error = false;
                    }, 2000);
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
    .mint-popup{
        width: 100%;
    }
</style>






