<template>
    <div>
        <div class="ques-content margin10">
            <div class="ques-item border">
                <div class="ques-icon">
                    <img src="../assets/images/name.png" alt="" class="ques-img">
                </div>
                <input type="text" class="ques-input" v-model="name" placeholder="请填写姓名">
            </div>
            <div class="ques-item border">
                <div class="ques-icon">
                    <img src="../assets/images/id.png" alt="" class="ques-img">
                </div>
                <input type="text" class="ques-input" v-model="idcard" placeholder="请填写身份证号">
            </div>
            <div class="ques-item border">
                <div class="ques-icon">
                    <img src="../assets/images/phone.png" alt="" class="ques-img">
                </div>
                <input type="tel" class="ques-input" v-model="phone" placeholder="请填写手机号码">
            </div>
            <div class="ques-item border right">
                <div class="ques-icon">
                    <img src="../assets/images/marriage.png" alt="" class="ques-img">
                </div>
                <select name="" id="" v-model="marry" class="ques-input">
                    <option value="">婚姻状况</option>
                    <option value="0">已婚</option>
                    <option value="1">未婚</option>
                </select>
            </div>
            <div class="ques-item">
                <div class="ques-icon">
                    <img src="../assets/images/certificate.png" alt="" class="ques-img">
                </div>
                <input type="text" class="ques-input" v-model="licence" placeholder="请填写营业执照上企业名称">
            </div>
        </div>
        <mt-popup v-model="error" class="mint-popup-2" position="top" :modal="false">
            {{errText}}
        </mt-popup>
        <div class="next-box">
            <div class="next" @click="next">下一步</div>
        </div> 
    </div>
</template>

<script>
export default {
    name: 'Ques1',
    data() {
        return {
            name: this.$store.state.data.name,
            idcard: this.$store.state.data.idcard,
            phone: this.$store.state.data.phone,
            marry: this.$store.state.data.marry,
            licence: this.$store.state.data.licence,
            error: false,
            errText: ''
        }
    },
    watch: {
        name: function(){
            this.$store.commit('NAME',this.name);
        },
        idcard: function(){
            this.$store.commit('IDCARD',this.idcard);
        },
        phone: function(){
            this.$store.commit('PHONE',this.phone);
        },
        marry: function(){
            this.$store.commit('MARRY',this.marry);
        },
        licence: function(){
            this.$store.commit('LICENCE',this.licence);
        },
        error: function(val) {
            if (val) {
                setTimeout(() => {
                    this.error = false;
                }, 2000);
            }
        }
    },
    methods: {
        next: function(){
            if(!this.name || !this.idcard || !this.phone || !this.marry || !this.licence){
                this.errText = '请将信息补充完整';
                this.error = true;
            }else if(!checkIdcard(this.idcard)){
                this.errText = '身份证填写有误';
                this.error = true;
            }else if(!(/^1[3|4|5|7|8][0-9]{9}$/.test(this.phone))){
                this.errText = '手机号填写有误';
                this.error = true;
            }else{
                if(24 < age(this.idcard) && age(this.idcard) <56 && checkWords(this.licence)){
                    if(this.marry == '0'){
                        this.$router.push('ques2');
                    }else if(this.marry == '1' && age(this.idcard) < 36){
                        this.$router.push('ques2');
                    }else{
                        this.submit();
                        this.$router.push('fail');
                    }
                }else{
                    this.submit();
                    this.$router.push('fail');
                }
                
            }
        }
    }
}

function checkWords(licence){
    if(licence.match(/[\u4e00-\u9fa5]{4,}/g) != null){
        return true;
    }else{
        return false;
    }
}

function age(identityCard) {

    var len = (identityCard + "").length;
    if (len == 0) {
        return 0;
    } else {
        if ((len != 15) && (len != 18))//身份证号码只能为15位或18位其它不合法
        {
            return 0;
        }
    }
    var strBirthday = "";
    if (len == 18)//处理18位的身份证号码从号码中得到生日和性别代码
    {
        strBirthday = identityCard.substr(6, 4) + "/" + identityCard.substr(10, 2) + "/" + identityCard.substr(12, 2);
    }
    if (len == 15) {
        strBirthday = "19" + identityCard.substr(6, 2) + "/" + identityCard.substr(8, 2) + "/" + identityCard.substr(10, 2);
    }
    //时间字符串里，必须是“/”
    var birthDate = new Date(strBirthday);
    var nowDateTime = new Date();
    var age = nowDateTime.getFullYear() - birthDate.getFullYear();
    //再考虑月、天的因素;.getMonth()获取的是从0开始的，这里进行比较，不需要加1
    if (nowDateTime.getMonth() < birthDate.getMonth() || (nowDateTime.getMonth() == birthDate.getMonth() && nowDateTime.getDate() < birthDate.getDate())) {
        age--;
    }
    return age;

}

function checkIdcard(personnumber) {

    personnumber = personnumber.toUpperCase();
    //身份证号码为15位或者18位，15位时全为数字，18位前17位为数字，最后一位是校验位，可能为数字或字符X。
    if (!(/(^\d{15}$)|(^\d{17}([0-9]|X)$)/.test(personnumber))) {
        return false;
    }
    //校验位按照ISO 7064:1983.MOD 11-2的规定生成，X可以认为是数字10。
    //下面分别分析出生日期和校验位
    var len, re;
    len = personnumber.length;
    if (len == 15) {
        re = new RegExp(/^(\d{6})(\d{2})(\d{2})(\d{2})(\d{3})$/);
        var arrSplit = personnumber.match(re);

        //检查生日日期是否正确
        var dtmBirth = new Date('19' + arrSplit[2] + '/' + arrSplit[3] + '/' + arrSplit[4]);
        var bGoodDay;
        bGoodDay = (dtmBirth.getFullYear() == Number(arrSplit[2])) && ((dtmBirth.getMonth() + 1) == Number(arrSplit[3])) && (dtmBirth.getDate() == Number(arrSplit[4]));
        if (!bGoodDay) {
            return false;
        }
        else {
            //将15位身份证转成18位
            //校验位按照ISO 7064:1983.MOD 11-2的规定生成，X可以认为是数字10。
            var arrInt = new Array(7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2);
            var arrCh = new Array('1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2');
            var nTemp = 0, i;
            personnumber = personnumber.substr(0, 6) + '19' + personnumber.substr(6, personnumber.length - 6);
            for (i = 0; i < 17; i++) {
                nTemp += personnumber.substr(i, 1) * arrInt[i];
            }
            personnumber += arrCh[nTemp % 11];
            return true;
        }
    }
    if (len == 18) {
        re = new RegExp(/^(\d{6})(\d{4})(\d{2})(\d{2})(\d{3})([0-9]|X)$/);
        var arrSplit = personnumber.match(re);

        //检查生日日期是否正确
        var dtmBirth = new Date(arrSplit[2] + "/" + arrSplit[3] + "/" + arrSplit[4]);
        var bGoodDay;
        bGoodDay = (dtmBirth.getFullYear() == Number(arrSplit[2])) && ((dtmBirth.getMonth() + 1) == Number(arrSplit[3])) && (dtmBirth.getDate() == Number(arrSplit[4]));
        if (!bGoodDay) {
            return false;
        } else {
            //检验18位身份证的校验码是否正确。
            //校验位按照ISO 7064:1983.MOD 11-2的规定生成，X可以认为是数字10。
            var valnum;
            var arrInt = new Array(7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2);
            var arrCh = new Array('1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2');
            var nTemp = 0, i;
            for (i = 0; i < 17; i++) {
                nTemp += personnumber.substr(i, 1) * arrInt[i];
            }
            valnum = arrCh[nTemp % 11];
            if (valnum != personnumber.substr(17, 1)) {
                return false;
            }
            return true;
        }
    }
    return false;
}
</script>

<style>

</style>




