<template>
    <div>
        <div style="width:100%; text-align: center; margin-bottom:25px;">
            <img class="successImg" src="../assets/images/成功.png"/>
            <div class="successText">恭喜您满足贷款条件</div>
        </div>
        <div style="width:100%; height:45px; background-color: #fff;">
            <div class="successText fLeft">预计您可以贷款</div>
            <div class="fRight"><span style="font-weight: 600">{{amount}}</span> 元</div>
        </div>
        <p class="prompt">以上金额根据您填写的信息测算，仅供参考，实际获批金额请以app上最终申请结果为准。</p>
        <div class="next-box">
            <router-link class="next" to="share">立即申请</router-link> 
        </div>
    </div>
</template>

<script>
export default {
    name: 'success',
    computed: {
        amount: function(){
            return this.$store.state.amount;
        }
    }
}
</script>
<style lang="scss">
@import '../assets/css/calculateResult.css';
.prompt{
    padding: 15px;
    text-align: left;
}
</style>






